body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    color: #fefafa;
    background-image: url('background.jpg'); /* Replace 'image-url.jpg' with the URL of your image */
    background-repeat: no-repeat; /* Prevent the image from repeating */
    background-size:cover; /* Scale the image to cover the entire container */
    background-attachment: fixed;
    background-position: center center; /* Center the image horizontally and vertically */
    background-color: #f0f0f0; /* Fallback color if the image isn't available */
  }
  h1 {
    text-align: center;
    margin-top: 50px;
  }
  
  p {
    margin-top: 20px;
    font-size: 22px;
    color: #fcf9f9c4;
  }
  
  
  .button-container {
    display: flex;
    justify-content: center;
  }
  
  .enter-button {
    background-color: green;
    color: white;
    font-size: 1.2rem;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
  }
  
  .claim-button {
    background-color: blue;
    color: white;
    font-size: 1.2rem;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
  }